




//- иконки
%icon {
	display: inline-block;
	vertical-align: middle;
}


%background-icon {
	background-position: center center;
	background-repeat: no-repeat;
	background-size: contain;
	max-width: 512px;
	max-height: 512px;
}


%icon-send { background-image: url( 'data:image/svg+xml;base64,' ); }
